@import url(https://fonts.googleapis.com/css?family=Barriecito|Roboto+Slab&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.home-header {
  font-family: "Barriecito", cursive;
  font-size: 4vw;
  color: #5e94fe;
  font-weight: bolder;
}
.home-footer {
  font-family: "Barriecito", cursive;
  font-size: 2vw;
  color: #5e94fe;
  font-weight: bolder;
  bottom: 0;
  right: 0;
  position: fixed;
  margin-right: 100px;
  margin-bottom: 15px;
}
.weatherpage {
  margin-left: 25%;
  margin-right: 25%;
  margin-top: 0%;
  margin-bottom: 25%;
}
.elipse1 {
  margin-left: 90%;
  position: fixed;
}
.elipse2 {
  position: fixed;
  bottom: -25px;
  margin-left: -10%;
}
.btn-modified {
  background-color: #ffdd8d;
  width: 50%;
  border-radius: 25px;
  border: 2px solid rgb(253, 253, 253);
  font-size: 15px;
  color: gray;
  font-weight: bold;
  height: 50px;
}
.group-home {
  margin-top: 10%;
  margin-left: 30%;
}
.form-control-m {
  width: 50%;
  border-radius: 25px;
  border: 2px solid #ffdd8d;
  padding: 20px;
  height: 50px;
}
.positionicon{
  cursor: pointer;
}
.position{
  color: rgb(0, 0, 0);
  margin-left: 30%;
  margin-top: 10%;
  color: #5e94fe;
}
@media only screen and (max-width: 758px) {
  /* Home page Mobile CSS*/
  .home-header {
    font-size: 7vw;
    color: #5e94fe;
    font-weight: bolder;
  }
  .home-footer {
    font-size: 5vw;
    color: #5e94fe;
    font-weight: bolder;
    margin-right: 20px;
    margin-bottom: 15px;
  }
  .elipse1 {
    margin-left: 70%;
    position: fixed;
  }
  .elipse2 {
    position: fixed;
    bottom: -25px;
    margin-left: -35%;
  }
  .btn-modified {
    background-color: #ffdd8d;
    width: 70%;
    border-radius: 25px;
    border: 2px solid rgb(253, 253, 253);
    font-size: 15px;
    color: gray;
    font-weight: bold;
    height: 50px;
  }
  .group-home {
    margin-top: 40%;
    margin-left: 5%;
  }
  .form-control-m {
    width: 67%;
    border-radius: 25px;
    border: 2px solid #ffdd8d;
    padding: 20px;
    height: 50px;
  }
  .positionicon{
    cursor: pointer;
  }
  .position{
    color: rgb(0, 0, 0);
    margin-left: 10%;
    margin-top: 30%;
    color: #5e94fe;
  }
  /*Weather Mobile CSS */
  .degree {
    float: left;
  }
  .w {
    width: 50px;
  }
  .weatherpage {
    margin-left: 0%;
    margin-right: 0%;
    margin-top: 0%;
    margin-bottom: 0%;
  }
  .e5dem {
    color: rgb(0, 0, 0);
    font-weight: bolder;
    font-size: 1em;
  }
}

